import React, { Component } from 'react';

import {Card, CardColumns, Col, Row} from 'react-bootstrap'
import {Link} from 'react-router-dom'

class Albums extends Component {
    constructor(props){
        super(props);

        this.state={
            data:null,
            type:null
        }
    }

    componentWillReceiveProps(nextProps){
        if(this.props.type !== nextProps.type){
            console.log(this.props.type)
            this.setState({type:nextProps.type})
            this.getdata()
        }
    }

    getdata(){
        let mycomp = this;
        fetch('/data/data.json').then(
            function(response){
                return response.json()
            }
        ).then(
            function(responseData){
                // console.log(responseData)
                mycomp.setState({data:responseData[mycomp.state.type]})
            }
        ).catch(
            function(error){
                console.log(error)
            }
        )
    }

    componentDidMount(){
        this.setState({type:this.props.type})
        this.getdata()
    }

    render(){
        
        // console.log(this.state.type)
        // console.log(this.state.data)
        var groups = [];
        var group = 0;
        if(this.state.data != null){
            for(group=0;group<this.state.data.groups.length;group++)
            {
                var items,i;
                items=[];
                var group_index = this.state.data.groups[group];
                for(i=0;i<group_index.length;i++){
                    // console.log(this.state.data.homepage[i])
                    //console.log("imdb" in cardata)
                    var k = 0;
                    var index;
                    for(k=0;k<this.state.data.content.length;k++)
                    {
                        if(this.state.data.content[k].id === group_index[i])
                        {
                            index = k;
                            break;
                        }
                    }
                    
                    var cardata = this.state.data.content[index]
                    console.log(cardata)
                    var _url="/"+this.state.type.substring(0,this.props.type.length-1)+"/"+cardata.id
                    var outlink = false
                    if("imdb" in cardata){
                        _url = cardata.imdb
                        //console.log(cardata.imdb)
                        outlink = true
                    }
                    //console.log(_url)
                    console.log(cardata.font.title)
                    if(outlink === false){
                        items.push(
                            <Link key={_url} to={_url} style={{color:"black"}}>
                                <Card border="light" className="bg-dark text-white">
                                    <Card.Img variant="top" src={cardata.src} alt={cardata.src} />
                                    <div style={{background:"white", lineHeight:"13pt", paddingTop:"3pt"}}>
                                        <b style={{fontFamily:cardata.font.title, color:cardata.font.color}}>{cardata.title}</b>
                                        <br />
                                        <t style={{fontFamily:cardata.font.text, color:cardata.font.color, fontSize:"10pt"}}>
                                        {cardata.text}
                                        </t>
                                    </div>
                                </Card>
                            </Link>
                        )
                    }
                    else
                    {
                        items.push(
                            <a href={_url} key={_url} target="_blank" >
                                <Card border="light" className="bg-dark text-white">
                                    <Card.Img variant="top" src={cardata.src} alt={cardata.src} />
                                    <div style={{background:"white", lineHeight:"13pt", paddingTop:"3pt"}}>
                                        <b style={{fontFamily:cardata.font.title, color:cardata.font.color}}>{cardata.title}</b>
                                        <br />
                                        <t style={{fontFamily:cardata.font.text, color:cardata.font.color, fontSize:"10pt"}}>
                                        {cardata.text}
                                        </t>
                                    </div>
                                </Card>
                            </a>
                        )
                    }
                    
                }
                groups.push(
                    <div style={{paddingTop:"35pt"}}>
                        <b style={{fontSize:"20pt", fontFamily:this.state.data.group_font}}><center>{this.state.data.content_group[group]}</center></b>
                        <CardColumns style={{paddingTop:"15pt"}}>
                            {items}
                        </CardColumns>
                    </div>
                    
                )
            }
        } 
        return (
            <Row style={{minHeight:"80vh",justifyContent:"center",alignItems:"center"}}>
            <Col style={{borderColor:"black"}}>
            {groups}
            </Col>
            </Row>
        );
    }
};

export default Albums;